p.MuiTypography-root.MuiTypography-body1 {
    font-size: 15px;
}
.MuiPaper-root.makeStyles-header-6.MuiPaper-elevation0 h1 {
    font-size: 3rem;
}

svg.MuiSvgIcon-root {
    background-color: white;
    border-radius: 50%;
}
svg.MuiSvgIcon-root path{
    color: black;
}

.MuiPaper-root.MuiMobileStepper-root.MuiMobileStepper-positionStatic.MuiPaper-elevation0 {
    background-color: #eeeeee00;
}
.bookingbtn{
    color: black;
    padding: 5px;
    outline: none;
    border: none;
    border-radius: 5px;
    background-color: rgb(250, 149, 34);
    font-size: 15px;
    transition: 0.3s;
    color: white;
} 
.bookingbtn:hover{
    background-color: rgb(196, 109, 10);
}  
.bookingbtn a{
    color: white;
    text-decoration: none;
}
.bt{
    margin-top: 100px;
    margin-left: 30px;
  
}
